<style lang="less" scoped>
  .dialog-body{
    .el-form-item__label{
      font-size: 12px;
      color: #666;
      line-height: 14px;
      padding: 0;
    }
    .el-form-item__content{
      .el-select{
        width: 100%;
      }
    }
    .el-input__inner{
      padding: 0 8px;
      border: 1px solid #dadde2;
      font-size: 12px;
      &:focus{
        border-color: #409EFF;
      }
    }
    .el-textarea__inner{
      padding: 5px 8px;
    }
    .sys-icon{
      width: 60px;
      height: 60px;
      border: 1px solid #dadde2;
      border-radius: 4px;
      margin-top: 27px;
      padding: 10px;
    }
    @{global} {
      .el-form--label-top .el-form-item__label {
        padding: 0;
      }
      .el-textarea__inner, .el-input__inner {
        font-size: 12px;
      }
    }
  }
  .table-filters .filter-item {
    width: 200px;
  }
  .w100 {
    width: 100%;
  }
  .addSystemBtn{
    cursor: pointer;
  }
</style>

<template>
  <div class="sysManage">
    <top-header title="系统管理">
       <div class="el-icon-plus addSystemBtn" @click="onAdd"> 新增系统</div>
    </top-header>
    <div class="page-container">
       <div class="table-filters">
        <div class="filter-item">
          <p class="item">系统名称</p>
           <el-input placeholder="请输入" size="small" clearable v-model="sysSearch.name"></el-input>
        </div>
        <div class="filter-item">
          <p class="item">接入方式</p>
          <el-select v-model="sysSearch.access_type" size="small">
            <el-option label="不限" :value="undefined"></el-option>
            <el-option v-for="item in accessType" :value="item.id" :label="item.name" :key="item.id"></el-option>
          </el-select>
        </div>
        <div class="filter-item">
          <p class="item">系统状态</p>
          <el-select v-model="sysSearch.status" size="small">
            <el-option label="不限" :value="undefined"></el-option>
            <el-option v-for="item in sysStatus" :value="item.id" :label="item.name" :key="item.id"></el-option>
          </el-select>
        </div>
        <div class='search-btn'>
          <el-button type="primary" size="small" icon="el-icon-search" @click="handleSearch">查询</el-button>
        </div>
      </div>
      <el-table
        :data="sysData"
        size="mini"
        style="width: 100%">
        <el-table-column
            type="index"
            label="序号"
            width="60">
          <template slot-scope="scope">
            {{(page.current - 1) * page.size + scope.$index + 1}}
          </template>
        </el-table-column>
        <el-table-column
          prop="name"
          label="系统名称">
        </el-table-column>
        <el-table-column
          label="系统状态">
          <template slot-scope="scope">
            <span :style="{color: scope.row.status === '1' ? '#e96767': '#00c353'}">{{ getSysStatus(scope.row.status) }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="接入方式">
          <template slot-scope="scope">
            {{ getAccessType(scope.row.access_type) }}
          </template>
        </el-table-column>
        <el-table-column
          prop="bind_user_num"
          label="用户数量">
        </el-table-column>
        <el-table-column
          prop="user_name"
          label="系统负责人">
        </el-table-column>
        <el-table-column
          prop="remark"
          label="备注">
        </el-table-column>
        <el-table-column
          label="操作"
          width="60">
          <template slot-scope="scope">
            <el-button type="text" size="mini" @click="onRevise(scope.row.id)">修改</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        v-show="page.total > page.size"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="page.sizes"
        layout="sizes,prev,pager,next"
        :current-page.sync="page.current"
        :total="page.total"
        prev-text="上一页"
        next-text='下一页'>
      </el-pagination>
    </div>

    <kye-dialog width="448px" @is-show-dialog="isShowDialog" @reset="resetForm('ruleForm')" :hasDialog="!!kyeDialog.hasDialog" :title="kyeDialog.title">
      <div slot="body" class="dialog-body">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-position="top" label-width="80px">
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item size="small" label="系统名称" prop="name">
                <el-input placeholder="请输入系统名称" v-model="ruleForm.name"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item size="small" label="系统状态" prop="status">
                <el-select v-model="ruleForm.status" placeholder="请选择系统状态">
                  <el-option v-for="item in formStatus" :value="item.id" :label="item.name" :key="item.id"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item size="small" label="系统负责人" prop="user_id">
                <el-autocomplete
                  class="w100"
                  ref="inputRef"
                  clearable
                  :fetch-suggestions="queryUser"
                  placeholder="请输入系统负责人"
                  v-model="stateUser"
                  :trigger-on-focus="false"
                  @select="handleSelectUser"
                >
                  <i
                    class="el-icon-search el-input__icon"
                    slot="suffix">
                  </i>
                  <template slot-scope="{ item }">
                    <div class="name text-overflow-ellipsis" :title="`${item.name}，${item.department_name}`">{{ `${item.name}，${item.department_name}` }}</div>
                  </template>
                </el-autocomplete>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item size="small" label="接入方式" prop="access_type">
                <el-select v-model="ruleForm.access_type" placeholder="请选择接入方式" @change="accessTypeChange">
                  <el-option v-for="item in accessType" :value="item.id" :label="item.name" :key="item.id"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item size="small" label="系统类别" prop="type">
                <el-select v-model="ruleForm.type" placeholder="请选择系统类别">
                  <el-option v-for="item in formType" :value="item.id" :label="item.name" :key="item.id"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item size="small" label="系统URL" prop="url">
                <el-input placeholder="请输入系统URL" v-model="ruleForm.url"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="24">
              <el-form-item class="login-type-group" size="small" label="默认登录方式">
                <el-select v-model="ruleForm.default_login" placeholder="请选择登录方式" :clearable="true">
                  <el-option :value="1" label="账号密码登录" :disabled="loginOptiondisabled1"></el-option>
                  <el-option :value="2" label="手机验证码登录" :disabled="loginOptiondisabled2"></el-option>
                  <el-option :value="3" label="账号密码登录或手机验证码登录" :disabled="loginOptiondisabled2"></el-option>
                  <el-option :value="4" label="账号密码登录和手机验证码登录" :disabled="loginOptiondisabled2"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="24">
              <el-form-item size="small" label="系统备注" name="remark">
                <el-input type="textarea" :autosize="{ minRows: 1, maxRows: 3 }" placeholder="请输入系统备注" v-model="ruleForm.remark"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item size="small" label="系统图标" prop="icon">
                <el-select v-model="ruleForm.icon" placeholder="请选择系统图标">
                  <el-option v-for="item in itemsIcon" :value="item.icon" :label="item.name" :key="item.id"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <div class="sys-icon">
                <svg class="icon" aria-hidden="true">
                  <use :xlink:href="'#icon-' + (ruleForm.icon || 'default-img')"></use>
                </svg>
              </div>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="24">
              <el-form-item size="small" label="系统IP" prop="ip_address">
                <el-input v-model="ruleForm.ip_address" type="textarea" :rows="6" placeholder="输入系统调用认证服务器所使用的IP地址，每个IP地址占一行。"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <div slot="footer">
        <el-button size="small" @click="kyeDialog.hasDialog = false">取消</el-button>
        <el-button type="primary" size="small" @click="submitForm('ruleForm')">保存</el-button>
      </div>
    </kye-dialog>
  </div>
</template>

<script>
import API from '@/api'
import TopHeader from '@/components/header'
import KyeDialog from '@/components/dialog'

export default {
  components: {
    TopHeader,
    KyeDialog
  },
  data () {
    return {
      value: undefined,
      sysSearch: {
        name: undefined,
        access_type: undefined,
        status: undefined
      },
      sysData: [],
      sysStatus: [
        {
          id: 1,
          name: '已启用'
        }, {
          id: 2,
          name: '已禁用'
        }
      ],
      accessType: [
        {
          id: 1,
          name: 'CAS'
        }, {
          id: 2,
          name: 'LDAP'
        }
      ],
      page: {
        current: 1,
        sizes: [10, 20, 50, 100],
        size: 10,
        total: 0
      },
      kyeDialog: {
        hasDialog: false,
        title: '新增系统'
      },
      stateUser: undefined,
      ruleForm: {
        id: undefined,
        user_id: undefined,
        name: undefined,
        status: undefined,
        access_type: undefined,
        type: undefined,
        url: undefined,
        remark: undefined,
        icon: undefined,
        ip_address: undefined,
        default_login: undefined
      },
      loginOptiondisabled1: false,
      loginOptiondisabled2: false,
      formStatus: [
        {
          id: 1,
          name: '启用'
        }, {
          id: 2,
          name: '禁用'
        }
      ],
      formType: [
        {
          id: 1,
          name: '基本系统'
        }, {
          id: 2,
          name: '业务系统'
        }
      ],
      itemsIcon: [
        {
          id: 1,
          name: '邮箱',
          icon: 'email'
        }, {
          id: 2,
          name: '网络准入',
          icon: 'permission'
        }, {
          id: 3,
          name: 'VPN',
          icon: 'vpn'
        }, {
          id: 4,
          name: '堡垒机',
          icon: 'bastion'
        }, {
          id: 5,
          name: 'WIKI',
          icon: 'wiki'
        }, {
          id: 6,
          name: '禅道',
          icon: 'chandao'
        }, {
          id: 7,
          name: '安全管理平台',
          icon: 'safe'
        }, {
          id: 8,
          name: '运维开发平台',
          icon: 'operations'
        }, {
          id: 9,
          name: '金蝶',
          icon: 'kingdee'
        }, {
          id: 10,
          name: 'ERP',
          icon: 'erp'
        }, {
          id: 11,
          name: '绩效管理平台',
          icon: 'performance'
        }, {
          id: 12,
          name: '客服系统',
          icon: 'service'
        }, {
          id: 13,
          name: 'OA',
          icon: 'oa'
        }, {
          id: 14,
          name: '电商运营后台',
          icon: 'mall'
        }, {
          id: 15,
          name: '人力资源系统',
          icon: 'hr'
        }, {
          id: 16,
          name: '资产管理系统',
          icon: 'assets'
        }
      ],
      rules: {
        name: [
          { required: true, message: '请输入系统名称', trigger: 'change' }
        ],
        status: [
          { required: true, message: '请选择系统状态', trigger: 'change' }
        ],
        user_id: [
          { required: true, message: '请输入系统负责人', trigger: 'change' }
        ],
        access_type: [
          { required: true, message: '请选择接入方式', trigger: 'change' }
        ],
        type: [
          { required: true, message: '请选择系统类型', trigger: 'change' }
        ],
        url: [
          { required: false, message: '请输入系统链接', trigger: 'change' }
        ],
        icon: [
          { required: true, message: '请选择系统图标', trigger: 'change' }
        ],
        ip_address: [
          { required: true, message: '请输入系统IP', trigger: 'change' }
        ],
        default_login: [
          { required: true, message: '请选择登录方式', trigger: 'change' }
        ]
      }
    }
  },
  watch: {
    // 删除用户过滤信息
    stateUser (val) {
      if (val === undefined || val === '') {
        this.ruleForm.user_id = undefined
      }
    },
    hasDetail (val, oldVal) {
      if (!val) {
        this.stateUser = undefined
        this.ruleForm.id = undefined
        this.ruleForm.user_id = undefined
        this.ruleForm.name = undefined
        this.ruleForm.status = undefined
        this.ruleForm.access_type = undefined
        this.ruleForm.type = undefined
        this.ruleForm.url = undefined
        this.ruleForm.remark = undefined
        this.ruleForm.icon = undefined
        this.ruleForm.ip_address = undefined
      }
    }
  },
  methods: {
    // 用户过滤查询
    queryUser (queryString, cb) {
      this.ruleForm.user_id = undefined
      API.getUserList({ name: queryString, per_page: 10, page: 1 }).then(res => {
        let result = res.result || {}
        cb(result.data || [])
      })
    },
    // 选择过滤的员工
    handleSelectUser (item) {
      this.stateUser = item.name
      this.ruleForm.user_id = item.id
    },
    // 获取列表数据
    requestList () {
      let params = {
        ...this.sysSearch,
        page: this.page.current,
        per_page: this.page.size
      }
      API.searchSys(params).then(res => {
        let result = res.result || {}
        this.page.total = result.pagination ? result.pagination.total_count : 0
        this.sysData = result.data || []
      })
    },
    // 获取系统状态
    getSysStatus (id) {
      let itemStatus = this.sysStatus.find(el => {
        return el.id === id
      })
      return itemStatus ? itemStatus.name : ''
    },
    // 获取系统状态
    getAccessType (id) {
      let itemType = this.accessType.find(el => {
        return el.id === id
      })
      return itemType ? itemType.name : ''
    },
    // 查询
    handleSearch () {
      this.page.current = 1
      this.requestList()
    },
    // 获取每页条数
    handleSizeChange (size) {
      this.page.size = size
      this.requestList()
    },
    // 获取当前页
    handleCurrentChange (index) {
      this.page.current = index
      this.requestList()
    },
    onAdd () {
      this.kyeDialog.title = '新增系统'
      this.resetForm('ruleForm')
      this.isShowDialog('add')
    },
    isShowDialog (bool) {
      this.kyeDialog.hasDialog = bool
      if (!bool) {
        this.resetForm('ruleForm')
      }
    },
    /**
     *@desc 新增默认登录方式
     *@author yuanwei
     *@date 2018-08-29
     **/
    accessTypeChange () {
      if (this.ruleForm.access_type === 2) {
        this.ruleForm.default_login = 1
        this.loginOptiondisabled2 = true
      } else {
        this.loginOptiondisabled2 = false
      }
    },
    loginTypeSelectChange (event) {
      console.log(this.ruleForm.default_login)
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let options = {...this.ruleForm}
          options.ip_address = options.ip_address.split(/\r\n|[\r\n]/).filter(item => item !== '').join(',')
          if (this.kyeDialog.hasDialog === 'add') {
            API.addSys(options).then(res => {
              this.kyeDialog.hasDialog = false
              this.$message.success('保存成功')
              this.requestList()
            })
          } else {
            API.putSys(options.id, options).then(res => {
              this.kyeDialog.hasDialog = false
              this.$message.success('保存成功')
              this.requestList()
            })
          }
        } else {
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName] && this.$refs[formName].resetFields && this.$refs[formName].resetFields()
    },
    onRevise (id) {
      this.kyeDialog.title = '修改系统'
      API.reviseSys(id).then(res => {
        console.log(res.result)
        let result = res.result || {}
        this.ruleForm.id = result.id || undefined
        this.ruleForm.user_id = result.user_id || undefined
        this.ruleForm.name = result.name || undefined
        this.ruleForm.status = result.status || undefined
        this.ruleForm.access_type = result.access_type || undefined
        this.ruleForm.type = result.type || undefined
        this.ruleForm.url = result.url || undefined
        this.ruleForm.default_login = result.default_login || undefined
        this.ruleForm.remark = result.remark || undefined
        this.ruleForm.icon = result.icon || undefined
        this.stateUser = result.user_name || undefined
        this.ruleForm.ip_address = result.ip_address ? result.ip_address.replace(/,/g, '\n') : undefined
        this.kyeDialog.hasDialog = 'edit'
      })
    }
  },
  beforeMount () {
    this.requestList()
  }
}
</script>



